	import {
	        mapState,  
	        mapMutations  
	    } from 'vuex'; 
export default {
	data() { 
		return {
			navList: [
				{txt: '我发布的',value: 1},
				{txt: '我参与的',value: 2}
			],
			activeTab: 1,
			loadingText: '',
			productList: [],
			statusList: [
				{ label: '租房中', value: 0, class: 'blue'},
				{ label: '租房完成', value: 10, class: 'green'},
				{ label: '租房解散', value: 20, class: 'red'}
			]
		}
	},
	computed: {
	    ...mapState(['emptyRoomPic'])  
	},
	methods: {
		toggleTab(tab) {
			this.activeTab = tab.value
			this.getList()
		},
		
		toGoods (id) {
			let activeTab = this.activeTab
			uni.navigateTo({
				url: `/pages/rent/detail?id=${id}&activeTab=${activeTab}`
			})
		},
		
		getList () {
			this.loadingText = '正在加载...'
			this.$api.getPersonalList({
				role: this.activeTab
			}).then(res => {
				res.data.forEach((item) => {
					item.imgUrlList = item.chamber_img_url && item.chamber_img_url.split(',') || []
				})
			   this.productList = res.data
			   if (!this.productList.length) {
				   this.loadingText = '暂无更多数据'
			   } else {
				  this.loadingText = '' 
			   }
			}).catch(err => {
			})
		}
	},
	onLoad (options){
		if (options && options.activeTab) {
			this.activeTab == 2
			this.getList()
		} else {
			this.getList()
		}
		this.$store.dispatch('getusermsg')
		this.$store.dispatch('cleanunread', {type: 1})
	},
	
	onShow() {
		// this.activeTab = 1
		// this.getList()
		this.$store.dispatch('cleanunread', {type: 1})
		uni.getStorage({
		    key: 'renttype',
		    success: (res) => {
				if (res && res.data && res.data == 2) {
					this.activeTab == 2
					this.getList()
				} else {
					this.getList()
				}
		    },
			fail: () => {
				this.getList()
			}
		});
		// uni.setStorage({key: 'renttype', data: '1'});
	}
}
